<template>
  <div :class="$root.themeHomeChange === '1' ? 'table-defect_light' : 'table-defect'">
    <div class="header">
      <el-button @click="goHome" class="btn" >返回</el-button>
      <span class="title">生成报告</span>
      <el-button type="primary" class="generateBtn" @click="generateCheckList" >生成报告</el-button>
    </div>
    
    <!-- 查询条件 -->
    <div class="selectBox">
      <div class="title">查询条件</div>
      <el-form
        :inline="true"
        :model="params">
        <el-form-item
          label="开始时间: ">
          <el-date-picker
            v-model="params.startTime"
            type="date"
            size="mini"
            placeholder="请选择"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            style="width: 180px;">
          </el-date-picker>
        </el-form-item>
        <el-form-item
          label="结束时间: ">
          <el-date-picker
            v-model="params.endTime"
            type="date"
            size="mini"
            placeholder="请选择"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            style="width: 180px;">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="责任部门: ">
          <el-select
            size="mini"
            v-model="params.zrbm"
            placeholder="请选择"
            style="width: 150px;"
            clearable
          >
            <el-option label="办公室" value="办公室"></el-option>
            <el-option label="合规与监审部" value="合规与监审部"></el-option>
            <el-option label="系统规划部" value="系统规划部"></el-option>
            <el-option label="资产管理部" value="资产管理部"></el-option>
            <el-option label="市场营销部" value="市场营销部"></el-option>
            <el-option label="安全监管部" value="安全监管部"></el-option>
            <el-option label="人力资源部" value="人力资源部"></el-option>
            <el-option label="党建工作部" value="党建工作部"></el-option>
            <el-option label="创新与数字化部" value="创新与数字化部"></el-option>
            <el-option label="财务部" value="财务部"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="项目类型: ">
          <el-select
            size="mini"
            v-model="params.xmlx"
            placeholder="请选择"
            style="width: 150px;"
          >
            <el-option label="迁改项目" value="迁改项目"></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="报告生成时间">
          <el-date-picker
            v-model="params.bgscsj"
            size="mini"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            style="width: 220px;"
          >
          </el-date-picker>
        </el-form-item> -->
        <el-form-item style="margin-left: 20px;">
          <el-button type="primary" size="mini">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    
    <!-- 数量Tag -->
    <div class="tagBox">
      <div class="title">缺陷数量</div>
      <div class="content">
        <el-row
          v-for="(item, index) in tagList"
          :key="index"
          :class="'item ' + item.class"
        >
          <el-col :span="4">
            <img :src="item.imgUrl" />
          </el-col>
          <el-col :span="20" class="msg">
            <p class="label">{{item.label}}</p>
            <p>
              {{item.num}}
              <span style="font-size: 12px;">个</span>
            </p>
          </el-col>
        </el-row>
      </div>
    </div>
    
    <!-- 表格内容 -->
    <div class="content">
      <el-table
        :data="tableData"
        style="width: 100%;"
        :height="tableHeight"
        tooltip-effect="light"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55"
          align="center">
        </el-table-column>
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="id"
          label="缺陷ID"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="qxdj"
          label="缺陷等级"
          width="80"
          align="center">
        </el-table-column>
        <el-table-column
          prop="rules"
          label="涉及检验逻辑"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="sjzd"
          label="涉及制度"
          min-width="160"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="explain"
          label="说明"
          min-width="140"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="zrr"
          label="责任人"
          width="85"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="tableData.length">
      </el-pagination>
    </div>
    <msgDialog
      ref="msgDialog"
      @confirm="generateConfirm"
      title="生成报告"
      :content="`
        <i class='el-icon-warning' style='font-size: 16px; color: orange;'></i> 
        此操作将生成已勾选的 ${this.multipleSelection.length} 条数据, 是否继续?
      `"/>
  </div>
</template>

<script>
  import moment from 'moment';
  import msgDialog from '@/views/components/dialog/msgDialog.vue';
  export default {
    name: "table-defect",
    components: { msgDialog },
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    data(){
      return {
        tableHeight: window.innerHeight - (300 + 70),
        tagList: [
          {
            imgUrl: require("@/assets/img/evaluationCenter/zdqx.png"),
            label: '重大缺陷',
            num: 0,
            class: 'zdqxTag'
          },
          {
            imgUrl: require("@/assets/img/evaluationCenter/jdqx.png"),
            label: '较大缺陷',
            num: 0,
            class: 'jdqxTag'
          },
          {
            imgUrl: require("@/assets/img/evaluationCenter/ybqx.png"),
            label: '一般缺陷',
            num: 0,
            class: 'ybqxTag'
          },
          {
            imgUrl: require("@/assets/img/evaluationCenter/qwqx.png"),
            label: '轻微缺陷',
            num: 0,
            class: 'qwqxTag'
          }
        ],
        params: {
          startTime: '',
          endTime: '',
          pageSize: 10,
          currentPage: 1
        },
        multipleSelection: [],
        tableData: [
          {
            id: '43212351',
            qxdj: '重大',
            rules: '工作许可人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》6.3.1.8 b',
            explain: '工作票签发人可由工作许可人兼任，但工作许可人和工作负责人不得相互兼任。除b）情况外，工作许可人不应签发工作票或担任工作班成员。',
            zrr: '陈小明'
          },
          {
            id: '43212352',
            qxdj: '重大',
            rules: '动火工作票签发人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》24.3.6.3',
            explain: '动火工作票签发人不得兼任动火工作负责人。',
            zrr: '杨伍'
          },
          {
            id: '43212353',
            qxdj: '较大',
            rules: '工作票签发人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》6.3.1.8',
            explain: '工作票签发人不应兼任该项工作的工作负责人。',
            zrr: '江小七'
          },
          {
            id: '43212354',
            qxdj: '较大',
            rules: '动火工作监护人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》24.3.6.3',
            explain: '动火工作票审批人、动火工作监护人不得签发动火工作票。',
            zrr: '陆小玖'
          },
          {
            id: '43212354',
            qxdj: '重大',
            rules: '工作许可人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》6.3.1.8 b',
            explain: '工作票签发人可由工作许可人兼任，但工作许可人和工作负责人不得相互兼任。除b）情况外，工作许可人不应签发工作票或担任工作班成员。',
            zrr: '江哲'
          },
          {
            id: '43212355',
            qxdj: '较大',
            rules: '工作票签发人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》6.3.1.8',
            explain: '工作票签发人不应兼任该项工作的工作负责人。',
            zrr: '陆小七'
          },
          {
            id: '43212356',
            qxdj: '较大',
            rules: '动火工作监护人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》24.3.6.3',
            explain: '动火工作票审批人、动火工作监护人不得签发动火工作票。',
            zrr: '陈虾'
          },
          {
            id: '43212357',
            qxdj: '较大',
            rules: '工作票签发人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》6.3.1.8',
            explain: '工作票签发人不应兼任该项工作的工作负责人。',
            zrr: '江瑞'
          },
          {
            id: '43212358',
            qxdj: '较大',
            rules: '动火工作监护人不相容分析',
            sjzd: '《中国南方电网有限责任公司电力安全工作规程》24.3.6.3',
            explain: '动火工作票审批人、动火工作监护人不得签发动火工作票。',
            zrr: '周星锐'
          },
        ]
      }
    },
    mounted() {},
    created() {
      this.initData();
    },
    watch: {
      tableData(v) {
        this.getTagData(v);
      },
    },
    methods: {
      // 初始化数据
      initData() {
        let endDate = new Date();
        let startDate = new Date().setMonth(endDate.getMonth() - 2);
        this.params.startTime = moment(startDate).format('YYYY-MM-DD');
        this.params.endTime = moment(endDate).format('YYYY-MM-DD');
        
        // 初始化Tag数量
        this.getTagData(this.tableData);
        // window.console.log(this.params);
      },
      // 获取tag数量（根据表格数据）
      getTagData(data) {
        this.tagList.forEach(item => {
          item.num = data.filter(resItem => {
            return item.label.includes(resItem.qxdj);
          }).length;
        });
      },
      goHome() {
        this.$router.push({
          path: '/tableReportView',
          query: {
            index: '5-2',
            actNav: this.$route.query.actNav
          }
        });
      },
      handleSizeChange(val) {
        window.console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        window.console.log(`当前页: ${val}`);
      },
      // 勾选表格
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // 生成勾选表格中的报告
      generateCheckList() {
        if(this.multipleSelection.length === 0) {
          this.$message({
            message: '暂无勾选内容，请勾选所要生成报告的数据 !',
            type: 'warning'
          });
          return false;
        }
        this.$refs['msgDialog'].openDialog();
      },
      // 确认生成报告
      generateConfirm() {
        this.$message({
          type: 'success',
          message: '报告生成中!  请稍后...'
        });
        window.console.log(`生成数据为：`, this.multipleSelection);
      }
    }
  }
</script>

